export default {
  name: 'Binsert',
  data () {
    return {}
  },
  methods: {
    bottomBtn () { // 申请白拿等按钮
      this.$emit('btn-click')
    },
    wantGet () { // 我要必中按钮
      this.$emit('wantGet')
    }
  },
  props: {
    leastNum: {
      type: Number,
      default: 1
    },
    type: {
      type: Number,
      default: 0
    },
    tType: {
      type: String,
      default: 'time'
    },
    leftTime: {
      type: String,
      default: '14:00开始'
    }
  }
}



